.home-page {
  width: 100%;
  height: 100vh;
  background: rgb(247, 248, 250);
  overflow-y: auto;
  *{
    box-sizing: border-box;
  }
  .inner{
    width: 1170px;
    margin: 0 auto;
  }
  .header {
    width: 100%;
    height: 64px;
    background: white;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.08);
    .inner{
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      img{
        width: 36px;
        height: 36px;
      }
      .top-nav{
        margin-left: 30px;
        height: 100%;
        line-height: 64px;
        .navBox{
          height: 100%;
          display: flex;
          .navLink{
            height: 100%;
            margin-right: 42px;
            color: #707070;
            &.active{
              color: #2672ff;
              border-bottom: 2px solid #2672ff;
            }
          }
        }
      }
      .tools{
        position: absolute;
        right: 0px;
        padding: 0 20px;
        height: 100%;
        line-height: 64px;
        top: 50%;
        transform: translateY(-50%);
        &:hover{
          cursor: pointer;
        }
        .iconfont{
          position: relative;
          padding-right: 8px;
          font-size: 20px;
        }
        
      }
    }
  }
  .page-view{
    padding: 24px 0 10px;
  }
}
